.ToastBox {
    width: 280px;
    height: 10px;
    background-color: transparent;
    position: fixed;
    top: calc(100vh - 0px);
    left: 8px;
	z-index: 3;
    transition: none;
}

.ToastBoxMessage {
    height: 36px;
    overflow: hidden;
    white-space: nowrap;
    animation-name: ToastBoxMessage;
    animation-duration: 5s;
    animation-iteration-count: initial;
    background-color: rgba(255,255,255,0.7);
    border-radius: 8px;
    margin-bottom: 10px;
    transition: all 0.3s;
}

.ToastBoxMessage:hover {
    background-color: rgba(255,255,255,1);
}

.ToastBoxMessageText {
    padding: 8px 1px 8px 13px;
    height: 20px;
    line-height: 20px;
    width: 228px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: rgba(0,0,0,0.7);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    font-size: 11px;
}

.ToastBoxMessageDone {
    float: right;
    width: 36px;
    height: 36px;
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(0, 0, 0, 1);'><path d='M1.293 1.293a1 1 0 0 1 1.414 0L8 6.586l5.293-5.293a1 1 0 1 1 1.414 1.414L9.414 8l5.293 5.293a1 1 0 0 1-1.414 1.414L8 9.414l-5.293 5.293a1 1 0 0 1-1.414-1.414L6.586 8 1.293 2.707a1 1 0 0 1 0-1.414z'></path></svg>");
    background-size: 12px;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    background-color: rgba(255,255,255,0);
    opacity: 0.7;
    transition: all 0.3s;
}

.ToastBoxMessageDone:hover {
    opacity: 1;
    background-color: rgba(255,255,255,1);
}