@font-face {
	font-family: 'Montserrat-Bold';
	src: url(./bin/fonts/Montserrat-Bold.woff);
}

@font-face {
	font-family: 'Montserrat-Light';
	src: url(./bin/fonts/Montserrat-Light.woff);
}

@font-face {
	font-family: 'Montserrat-Medium';
	src: url(./bin/fonts/Montserrat-Medium.woff);
}

@font-face {
	font-family: 'Montserrat-Regular';
	src: url(./bin/fonts/Montserrat-Regular.woff);
}

@font-face {
	font-family: 'Montserrat-Thin';
	src: url(./bin/fonts/Montserrat-Thin.woff);
}

* {
	user-select: none;
	outline: none;
	-webkit-tap-highlight-color: transparent;
	text-decoration: none;
	touch-action: none;
	font-family: 'Montserrat-Light';
}

body {
	background-color: #000;
	margin: 0px;
	overflow: hidden;
}

.ExhibitCanvas {
	position: fixed;
	z-index: 1;
}

.card {
	position: fixed;
	z-index: 2;
	padding: 15px;
	background-color: #FFF;
	border-radius: 12px;
	font-weight: bolder;
	font-size: 12px;
	cursor: pointer;
	transform: scale(1);
	transition-property: transform, opacity;
	transition-duration: 0.2s;
	opacity: 0.8;
}

#card_off {
	display: none;
}

.card:hover {
	transform: scale(1.2);
	opacity: 1;
}


.DetailBox {
	position: fixed;
	z-index: 6;
	width: 280px;
	height: 205px;
	background-color: #FFF;
	border-radius: 12px;
	top: 20px;
	left: 20px;
	display: none;
}

.DetailBoxHead {
	font-size: 20px;
	color: #000;
	font-weight: bold;
	padding: 20px;
}

.DetailBoxText {
	font-size: 13px;
	color: #222;
	font-weight: bold;
	padding: 0px 20px 20px 20px;
}

.DetailBoxLink, .DetailBoxBack {
	display: inline-block;
	margin-left: 20px;
	font-size: 13px;
	background-color: purple;
	color: #FFF;
	border-radius: 8px;
	padding: 10px;
	cursor: pointer;
}