input[placeholder] {
    position: fixed;
    z-index: 4;
    width: 30px;
    height: 50px;
    border: none;
    left: calc(100vw - 70px);
    top: 20px;
    box-shadow: 2px 2px 8px rgba(0,0,0,0.2);
    transition: all 0.2s;
    padding: 0px 10px;
    font-family: 'Montserrat-Bold';
    cursor: pointer;
    opacity: 0.8;
    border-radius: 8px;
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(0, 0, 0, 1);'><path d='M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z'></path></svg>");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 22px auto;
    color: transparent;
}

input[placeholder]:focus {
    width: 280px;
    left: calc(100vw - 320px);
    cursor: text;
    opacity: 1;
    background-image: none;
    color: #333;
}

input[placeholder]::placeholder {
    color: transparent;
    font-family: 'Montserrat-Medium';
}

input[placeholder]:focus::placeholder {
    color: #333;
}

input[placeholder]:not(:placeholder-shown) {
    width: 280px;
    left: calc(100vw - 320px);
    cursor: text;
    opacity: 1;
    background-image: none;
    color: #333;
}

.SearchBox {
    position: fixed;
    z-index: 4;
    width: 300px;
    left: calc(100vw - 320px);
    top: 80px;
    opacity: 0.8;
    border-radius: 8px;
    overflow: hidden;
}

.SearchBoxItem {
    line-height: 25px;
    padding: 8px 15px;
    font-size: 13px;
    font-family: 'Montserrat-Bold';
    cursor: pointer;
    transition: all 0.2s;
    opacity: 0.8;
    background-color: #FFF;
    color: #333;
}

.SearchBoxItem:hover {
    opacity: 1;
    color: #111;
}